// Funci√≥n para corregir caracteres UTF-8 mal codificados
export const fixUTF8 = (text) => {
  if (!text) return text;
  return text
    .replace(/√É¬°/g, '√°')
    .replace(/√É¬©/g, '√©')
    .replace(/√É¬≠/g, '√≠')
    .replace(/√É¬≥/g, '√≥')
    .replace(/√É¬∫/g, '√∫')
    .replace(/√É¬±/g, '√±')
    .replace(/√É/g, '√ë')
    .replace(/√É/g, '√Å')
    .replace(/√É/g, '√â')
    .replace(/√É/g, '√ç')
    .replace(/√É/g, '√ì')
    .replace(/√É/g, '√ö')
    .replace(/√É¬ß/g, '√ß')
    .replace(/√É¬ß/g, '√á')
    .replace(/√É¬º/g, '√º')
    .replace(/√É¬º/g, '√ú')
    .replace(/√É¬®/g, '√®')
    .replace(/√É¬®/g, '√à')
    .replace(/√É¬¨/g, '√¨')
    .replace(/√É¬¨/g, '√å')
    .replace(/√É¬≤/g, '√≤')
    .replace(/√É¬≤/g, '√í')
    .replace(/√É¬π/g, '√π')
    .replace(/√É¬π/g, '√ô');
};

// Funci√≥n de prueba
export const testUTF8Fix = () => {
  const testCases = [
    'Consolidacin unidimensional.',
    'Expansin libre.',
    'Expansin controlada Mtodo A.',
    'Compresin / Unidades de alba ilera de Arcilla.',
    'Medidas del rea de vacos en unidades perforadas.',
    'ndice de Durabilidad Agregado.',
    'Prctor modificado (*).',
    'Clasificacin suelo SUCS - AASHTO (*).'
  ];

  console.log('üß™ Probando correcci√≥n UTF-8:');
  testCases.forEach(test => {
    const fixed = fixUTF8(test);
    console.log(`Original: ${test}`);
    console.log(`Corregido: ${fixed}`);
    console.log('---');
  });
};
